.containerContentPageTwo{
    min-width: 100vw;
    min-height: 80vh;
    max-height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.titlePageTwo{
    width: 85vw;
    min-height: 20vh;
    max-height: 20vh;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #FFF;
    font-family:  Montserrat;
    font-size: 1.2rem;
    img{
        margin-right: 1%;
    }
}
.containerSubContentPageTwo{
    min-width:  85vw;
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    .subtitleContainerPageTwo{
        text-align: left;
        color: #FFF;
        font-family:  Montserrat;       
        h1{
            font-size: 2.8rem;
            font-stretch: condensed;
            margin-bottom: -1%;
        }
    }
}
.optionSelectSubtitle{
    margin-top: 1%;
    min-width:  85vw;
    display: flex;
    flex-direction: row;
    .submenuPageTwo{
        text-align: left;
        min-width: 55%;
        height: 40vh;
        display: flex;        
        align-items: center;
        font-family:  Montserrat;
        .submenu{
            width: 80%;
            list-style: none;
            color: #707070;
            font-size: 2rem;
            font-weight: 700;
            li{
                margin-left:10%;
                width: 80%;
                border-bottom: 1px solid #848484;
            }
            .suboptionSelected{
                color: #FF4700;
                //border-bottom: 1px solid #FFFFFF;
                border-bottom: 1px solid rgba(255,255,255, .3);
            }
        }
    }
    .contentMenuPageTwo{
        min-width: 40%;
        display: flex;

    }
}
