.containerContentPageFour{
    width: 100vw;
    height: 100vh;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.pageContentFour{
    width: 90vw;
    height: 90vh;
    border-top: 1px solid rgba(255,255,255, .3);
    //border-top: 1px solid #FFFFFF;
    .pageContentFourHeader{
        width: 90vw;
        min-height: 10vh;
        display: flex;
        flex-direction: row;
        .pageContentFourHeaderLeft{
            width: 50%;
            min-height: 10vh;
            display: flex;
            justify-content: center;
            align-items: flex-start;
        }
        .pageContentFourHeaderRight{
            width: 50%;
            min-height: 10vh;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            color: #FF4700;
            font-size: 1.5rem;
            font-family:  Montserrat;
        }
    }
