@mixin white-gradient {
	background: linear-gradient(to right,  rgba(255,255,255,0) 0%,rgba(255,255,255,0) 20%);
}

$animationSpeed: 40s;

// Animation
@keyframes scroll {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-250px * 7))}
}


// Styling
.slider {
	background-color: none;
	box-shadow: 0 10px 20px -5px rgba(0, 0, 0, .125);
	min-width: 100vw;
	min-height: 40vh;
	max-height: 40vh;
	margin: auto;
	overflow:hidden;
	position: relative;
	width: 960px;
	
	&::before,
	&::after {
		@include white-gradient;
		content: "";
		height: 100%;
		position: absolute;
		width: 200px;
		z-index: 2;
	}
	
	&::after {
		right: 0;
		top: 0;
		transform: rotateZ(180deg);
	}

	&::before {
		left: 0;
		top: 0;
	}
	
	.slide-track {
		animation: scroll $animationSpeed linear infinite;
		display: flex;
		width: calc(250px * 14);
	}
	
	.slide {
		min-height: 25vh;
		min-width: 18vw;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 10vw;
	}
	.slide:hover{
		transform: scale(1.2);
		margin-right: 10vw;
	}
}