.containerFormContact{
    min-height: 100vh;
    color: white;
    display: flex;
    padding-top: 20vh;
    flex-direction: column;
    align-items: center;

    .formTitle{
        width: 80%;
        border-top: 1px solid rgba(255,255,255, .3);
        display: flex;
        flex-direction: row;
        .formTitleLeft{
            width: 50%;
            display: flex;
            height: 8vh;
            align-items: center;
            justify-content: flex-start;
            font-family: Montserrat;
            text-align: right;
            width: 50%;
            font-size: 1.2rem;
        }
        .formTitleRight{
            display: flex;
            height: 8vh;
            align-items: center;
            justify-content: flex-end;
            font-family: Montserrat;
            text-align: right;
            width: 50%;
            font-size: 1.2rem;
            color: #FF4700;
        }
    }

    .containerFormContent{
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        width: 80vw;
        margin-top: 5vh;
        .containerFormContentLeft{
            display: flex;
            flex-direction: column;
            min-width: 40vw;
            
            .subtitleForm{
                min-width: 40vw;
                display: flex;
                flex-direction: row;
                text{
                    min-width: 20vw;
                    font-size: 1.1rem;
                    font-weight: 200;
                    font-family: Montserrat;
                    color: #FFFFFF;
                    text{
                        color: blue;
                    }
                }
            }
            .inputForm{
                min-width: 40vw;
                display: flex;
                flex-direction: row;
                input{
                    flex-grow: 1;
                    font-size: 1.2rem;
                    font-family: Montserrat;
                    min-height: 40px;
                    margin-top: 5px;
                    margin-right: 5px;
                    margin-bottom: 20px;
                    color: #FFFFFF;
                }
                .message{
                    min-height: 10vh;
                    display: flex;
                    justify-content: flex-start;
                    align-items: flex-start;
                }
            }
            border-bottom: 1px solid rgba(255,255,255, .3);
        }
        .containerFormContentRight{
            min-width: 40%;
            min-height: 60vh;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            font-family: SummerFaith;
            font-weight: 700;
            font-size: 8rem;
        }
    }
}